@import url(https://fonts.googleapis.com/css2?family=Martel+Sans:wght@300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: #141414;
}

.navbar{background-color:#141414}

.main{background-color:#141414}

@font-face{font-family:'Netflix Sans';font-weight:700;font-display:optional;src:url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff2) format("woff2"),url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff) format("woff")}.header{text-align:center}.headerLogo{height:200px;text-align:center;position:-webkit-sticky;position:sticky;background-color:#141414 !important;margin-bottom:-40px}.jumbotron{background-color:#141414 !important;font-family:"Netflix Sans","Helvetica Neue",Helvetica,Arial,sans-serif}.jumbotron .jumboTitle{font-size:24px;color:#777777}.jumbotron .jumboTitleTyped{font-size:48px;color:#777777}.jumbotron .jumboDesc{font-size:16px;font-weight:300;color:#4e4e4e !important}.jumbotron .jumboLogo{height:80px;width:80px;object-fit:cover}.leagues{margin-top:40px}

@font-face{font-family:'Netflix Sans';font-weight:700;font-display:optional;src:url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff2) format("woff2"),url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff) format("woff")}.card{margin:16px 0px;border:none !important;background-color:transparent !important;cursor:pointer;transition:.8s}.card:hover{transform:scale(1.3);z-index:1}.bg{opacity:0.2}.modal-body{background-color:#141414 !important;padding:0px !important}.modal-body .modalImage{width:100%;margin-bottom:16px}.modal-body .modalTitle{color:#e5e5e5;text-align:center;font-size:32px}.modal-body .modalRound{font-size:14px;color:#4e4e4e;text-align:center;margin-top:-16px}.modal-body .modalDate{margin:16px 0px;font-size:16px;font-weight:300;text-align:center;color:#e5e5e5}.modal-body .modalTime{margin:16px 0px;font-size:16px;font-weight:300;text-align:center;color:#e5e5e5}.modal-body .modalLinks{margin-top:40px;text-align:center}.modal-body .modalLinks .modalLinksDesc{margin-bottom:4px;color:#4e4e4e}.modal-body .modalLinks .modalActions{width:48px;height:48px;border-radius:50%}.modal-body .modalBtnContainer{text-align:center;margin-top:32px}.modal-body .modalBtnContainer .modalClose{border:1px solid #4e4e4e;width:80%;padding:8px 24px;background-color:#141414 !important;transition:.5s;color:#4e4e4e;margin-bottom:16px}.modal-body .modalMatchesContainer{text-align:center !important}.modal-body .modalMatchesContainer .modalLastMatchesTitle{margin-top:16px;font-size:16px;font-weight:300;text-align:center;color:#e5e5e5}

.green{color:white;margin:0px 4px;min-width:40px;border-radius:4px;background-color:#65BA52}.orange{color:white;margin:0px 4px;min-width:40px;border-radius:4px;background-color:#ecb34f}.red{color:white;margin:0px 4px;min-width:40px;border-radius:4px;background-color:#C33E38}.result{left:50%}

@font-face{font-family:'Netflix Sans';font-weight:700;font-display:optional;src:url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff2) format("woff2"),url(https://assets.nflxext.com/ffe/siteui/fonts/netflix-sans/v3/NetflixSans_W_Md.woff) format("woff")}.leagueContainer{margin:0px 48px;padding-top:40px;min-height:260px}.leagueContainer .leagueTitle{line-height:1.25vw;font-size:24px;color:#e5e5e5;font-family:"Netflix Sans","Helvetica Neue",Helvetica,Arial,sans-serif;font-weight:700;margin-bottom:0px;margin-left:-16px}.leagueContainer .fas{cursor:pointer;transition:.5s;float:right;margin-left:32px;margin-right:-16px}.leagueContainer .fas:hover{color:#e5e5e5}

